import type { CreateQueryRequest } from '@/types/query'

/**
 * Log a query to the user's query history
 * Silently fails if logging fails (non-blocking)
 */
export async function logQuery(request: CreateQueryRequest): Promise<void> {
  try {
    const response = await fetch('/api/queries', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(request)
    })

    if (!response.ok) {
      const data = await response.json()
      console.warn('Failed to log query:', data.error)
    }
  } catch (error) {
    // Silently fail - query logging should not block user actions
    console.warn('Error logging query:', error)
  }
}
